import './video.scss';
import YoutubeLogo from '../img/Black.svg';
import SearchIcon from '../img/search.svg'
import KameraIcon from '../img/phone-18.svg'
import CombinedShape from '../img/Combined Shape.svg'
import QongiroqIcon from '../img/ui-11.svg'
import MyUserImg from '../img/Userpic.svg'
import { useEffect, useRef, useState } from 'react';
import { NavLink } from 'react-router-dom';
import HomeIcon from '../img/ui-71.svg'
import TrendingIcon from '../img/ui-01.svg'
import HistoryIcon from '../img/others-07.svg'
import GamesIcon from '../img/tech-02.svg'
import LikedIcon from '../img/ui-03.svg'
import FavouritesIcon from '../img/ui-02.svg'
import WatchIcon from '../img/tech-25.svg'
import User1 from '../img/Oval (1).svg'
import User4 from '../img/Oval (4).svg'
import User5 from '../img/Oval (5).svg'
import useAuth from '../Hooks/useAuth';

function Home() {
    let sunicon = useRef()
    let moonicon = useRef()
    let tyomniy = useRef()
    let redfill = useRef()
    let container = useRef()
    const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  let [token, setToken] = useAuth()
  let counterlike = useRef()
  let counterdislike = useRef()
  let counter = useRef()
  let counter2 = useRef()
  let tyomniymode = useRef()
  let tyomniymode2 = useRef()
  let text = useRef()

  console.log(data);
  console.log(loading);

  let setObj = JSON.parse(token)

    useEffect(() => {
        fetch("https://jsonplaceholder.typicode.com/photos")
          .then((response) => response.json())
          .then((data) => {
            setData(data);
            setLoading(false);
          });
      }, []);
        return (
            <>
            <div ref={container} className="container">
            <header className="header">
                <div className="header__flex-1">
                <img className="header__logo" src={YoutubeLogo} alt="img"/>
                <i ref={sunicon} className="header__sun fa-regular fa-sun" onClick={(e) => {
                    tyomniy.current.classList.toggle('tyomniy')
                    if(tyomniy.current.classList.contains('tyomniy')){
                        moonicon.current.style.display = 'none';
                        e.target.style.display = 'inline-block'
                        tyomniymode.current.style.backgroundColor = '#2E2E34'
                        tyomniymode2.current.style.backgroundColor = '#2E2E34'
                     }else {
                         moonicon.current.style.display = 'inline-block';
                         e.target.style.display = 'none'
                         tyomniymode2.current.style.backgroundColor = '#EBEBEB'
                         tyomniymode.current.style.backgroundColor = '#EBEBEB'
                     }
                 }}/>
                <button className="btn__sun" onClick={() => {
                    tyomniy.current.classList.toggle('tyomniy')
                    if(tyomniy.current.classList.contains('tyomniy')){
                        moonicon.current.style.display = 'none';
                        sunicon.current.style.display = 'inline-block'
                        tyomniymode2.current.style.backgroundColor = '#2E2E34'
                        tyomniymode.current.style.backgroundColor = '#2E2E34'
                     }else if(!tyomniy.current.classList.contains('tyomniy')){
                         moonicon.current.style.display = 'inline-block';
                         sunicon.current.style.display = 'none'
                         tyomniymode2.current.style.backgroundColor = '#EBEBEB'
                         tyomniymode.current.style.backgroundColor = '#EBEBEB'
                     }
                 }}/>
                <i ref={moonicon} className="header__moon fa-regular fa-moon" onClick={(e) => {
                    tyomniy.current.classList.toggle('tyomniy')
                    if(tyomniy.current.classList.contains('tyomniy')){
                     e.target.style.display = 'none';
                     sunicon.current.style.display = 'inline-block'
                     tyomniymode2.current.style.backgroundColor = '#2E2E34'
                     tyomniymode.current.style.backgroundColor = '#2E2E34'
                 }
             }}/>
                </div>
             <div className="header__flex-2">
             <input className="header__input" type="text" placeholder="Search"/>
             <img className="header__icon" src={SearchIcon} alt="img" />
             </div>
                <div className="header__flex-3">
                <img className="header__kamera" src={KameraIcon} alt="img" />
                <img className="header__shape" src={CombinedShape} alt="img" />
                <img className="header__qongiroq" src={QongiroqIcon} alt="img" />
                <img className="header__user--img" src={MyUserImg} alt="img" />
                </div>
            </header>
            <footer className="footer">
                <div className="footer__flex">
                <ul className="footer__list">
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/">
                            <img ref={redfill} className="footer__icon" src={HomeIcon} alt="img" />
                            <h2 className="footer__link">Home</h2>
                            </NavLink>
                    </li>
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/trending">
                            <img ref={redfill} className="footer__icon left" src={TrendingIcon} alt="img" />
                            <h2 className="footer__link">Trending</h2>
                            </NavLink>
                    </li>
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/history">
                            <img ref={redfill} className="footer__icon" src={HistoryIcon} alt="img" />
                            <h2 className="footer__link">History</h2>
                            </NavLink>
                    </li>
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/watch">
                            <img ref={redfill} className="footer__icon right" src={WatchIcon} alt="img" />
                            <h2 className="footer__link">Watch later</h2>
                            </NavLink>
                    </li>
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/like">
                            <img ref={redfill} className="footer__icon " src={LikedIcon} alt="img" />
                            <h2 className="footer__link">Liked videos</h2>
                            </NavLink>
                    </li>
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/favourites ">
                            <img ref={redfill} className="footer__icon" src={FavouritesIcon} alt="img" />
                            <h2 className="footer__link">Favourites</h2>
                            </NavLink>
                    </li>
                    <li className="footer__item">
                        <NavLink className="footer__navlink" to="/games">
                            <img ref={redfill} className="footer__icon " src={GamesIcon} alt="img" />
                            <h2 className="footer__link">Games</h2>
                            </NavLink>
                    </li>
                </ul>
                <ul className="footer__list2">
                    <h1 className="footer__title1">Subscriptions</h1>
                    <li className="footer__item2" onClick={() => {
                        let datatoken = {
                            name: 'Gussie Singleton',
                            img: User1
                        }
                        setToken(JSON.stringify(datatoken))
                    }}>
                        <NavLink className="footer__navlink2" to="/channel">
                        <img className="footer__img2" src={User1} alt="img" />
                        <h2 className="footer__title">Gussie Singleton</h2>
                        </NavLink>
                    </li>
                    <li className="footer__item2" onClick={() => {
                        let datatoken = {
                            name: 'Emma Hanson',
                            img: User5
                        }
                        setToken(JSON.stringify(datatoken))
                    }}>
                        <NavLink className="footer__navlink2" to="/channel">
         <img className="footer__img2" src={User5} alt="img" />
                        <h2 className="footer__title">Emma Hanson</h2>
                        </NavLink>
                    </li>
                    <li className="footer__item2" onClick={() => {
                        let datatoken = {
                            name: 'Eunice Cortez',
                            img: User4
                        }
                        setToken(JSON.stringify(datatoken))
                    }}>
                        <NavLink className="footer__navlink2" to="/channel" >
                        <img className="footer__img2" src={User4} alt="img" />
                        <h2 className="footer__title">Eunice Cortez</h2>
                        </NavLink>
                    </li>
                </ul>
                </div>
            </footer>
            </div>
            <div ref={tyomniy} className="yarkiy nimadur">
                <div className="container">
                <iframe className="video__iframe" style={{borderRadius: 14, marginLeft: 262}} src="https://www.youtube.com/embed/RGoPU-OLQHE" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen/>
                    <h2 className="video__title">High Quality Aerial Video 8K HDR 60FPS (FUHD)</h2>
                    <p className="video__text">5 980 158 watching...</p>
                    <div className="video__div">
                    <span ref={tyomniymode2} className="video__span" onClick={(e) => {
                        e.target.classList.toggle('counter')
                        if(e.target.classList.contains('counter')){
                            counterlike.current.style.color = '#ff0000'
                            counter.current.textContent = '1152'
                        }else{
                            counterlike.current.style.color = '#ccc'
                            counter.current.textContent = '1151'
                        }
                    }}>
                    <i ref={counterlike} className="video__like fa-regular fa-thumbs-up"></i>
                    <p ref={counter} className="video__counter">1151</p>
                    </span>
                    <span ref={tyomniymode} className="video__span" onClick={(e) => {
                        e.target.classList.toggle('counter')
                        if(e.target.classList.contains('counter')){
                            counterdislike.current.style.color = '#ff0000'
                            counter2.current.textContent = '122'
                        }else{
                            counterdislike.current.style.color = '#ccc'
                            counter2.current.textContent = '121'
                        }
                    }}>
                    <i ref={counterdislike} className="video__dislike fa-regular fa-thumbs-down"></i>
                    <p ref={counter2} className="video__counter">121</p>
                    </span>
                    <span className="video__user--span">
                        <img className="video__user--img" src={setObj.img} alt="img" />
                        <h2 className="video__user--title">{setObj.name}</h2>
                    </span>
                    <p className="video__user--text">76 тыс. подписчиков</p>
                    <p className="video__users--text">A successful marketing plan relies heavily on the pulling-power of advertising copy. <br />Writing result-oriented ad 
                    copy is difficult, as it must appeal to, entice, and convince <br />consumers to take action.There is no 
                    magic formula to write perfect ad copy; it is based </p>
                    <p ref={text} className="video__users--text2">A successful marketing plan relies heavily on the pulling-power of advertising copy. <br />Writing result-oriented ad 
                    copy is difficult, as it must appeal to, entice, and convince <br />consumers to take action.There is no 
                    magic formula to write perfect ad copy; it is based </p>
                    <button className="video__users--btn" onClick={(e) => {
                        e.target.classList.toggle('nimadur')
                        if(e.target.classList.contains('nimadur')){
                            e.target.textContent = 'Close'
                            text.current.style.display = 'inline-block'
                        }else {
                            e.target.textContent = 'Show More'
                            text.current.style.display = 'none'
                        }
                    }}>Show More</button>
                    <ul className="video__user--list">
                        {data.length && data.slice(0, 4).map((row, index) => (
                            <li key={index} className="video__user--item">
                            <NavLink style={{textDecoration: 'none'}} to={"/video/" + row.id}>
                            <img className="video__list--img" src={row.thumbnailUrl} alt="img" />
                            <h2 className="video__list--title">{row.title}</h2>
                            </NavLink>
                        </li>
                        ))}
                    </ul>
                    </div>
                </div>
            </div>
            </>
           )
}
export default Home